import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/HydrogenLuminess/1.jpg"
import image2 from "../../static/HydrogenLuminess/2.jpg"
import image3 from "../../static/HydrogenLuminess/3.jpg"
import image4 from "../../static/HydrogenLuminess/4.jpg"
import image5 from "../../static/HydrogenLuminess/5.jpg"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Hydrogen</h1>
        
        <img class="hydlum" src={image1} alt="image1"/>
        <img class="hydlum" src={image2} alt="image2"/>
        <img class="hydlum" src={image3} alt="image3"/>
        <img class="hydlum" src={image4} alt="image4"/>
        <img class="hydlum" src={image5} alt="image5"/>

        <div>
          <p>
          Website conmtaining all scenarios: <a href="https://e-deffinum.org"> E-Deffinum </a>
          <br></br>
          This is one of the projects I have worked on while at Luminess. It is a VR scenario intended to teach workers in a Hydrogen factory how to deal with certain scenarios.
          In some cases gas leaks, in some cases just what equipment should they have equiped or at hand. I have personally implemented 3 of the 6 scenarios in the projects from scratch, but I have modified, improved and fixed all of them.
          <br></br>
          Each scenario has minigames and\/or quizzes intented to test and teach the user about the factory and working with Hydrogen. 
          <br></br>
          The game is made in Unity using C#, tested and intented for Oculus 2 headsets, but functional on others as well. All scenarios have a login screen intended to connect to a Moodle server and record a user's progress.
          <br></br>
          Youtube Link: <a href="https://www.youtube.com/watch?v=bc4fe2N_b1o"> Hydrogen scenario example </a>
          </p>
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage